@layer base {
  :root {
    --background: 0 0% 0%;
    --foreground: 0 0% 100%;
    --card: 0 0% 10%;
    --card-foreground: 0 0% 100%;
    --primary: 250 90% 55%;
    --primary-foreground: 0 0% 100%;
    --secondary: 280 85% 55%;
    --secondary-foreground: 0 0% 100%;
    --accent: 200 100% 50%;
    --muted: 0 0% 30%;
    --muted-foreground: 0 0% 70%;
    --border: 0 0% 20%;
    --input: 0 0% 15%;
    --ring: 250 90% 55%;
  }
  * {
    border-color: hsl(var(--border));
  }
  html {
    scroll-behavior: smooth;
  }
  body {
    background-color: hsl(var(--background));
    color: hsl(var(--foreground));
    background-image: radial-gradient(circle at 20% 50%, rgba(139, 92, 246, 0.15) 0%, transparent 50%),
      radial-gradient(circle at 80% 80%, rgba(59, 130, 246, 0.15) 0%, transparent 50%);
  }
}
@layer utilities {
  .bg-gradient-primary {
    background: linear-gradient(to right, rgb(147, 51, 234), rgb(59, 130, 246), rgb(34, 211, 238));
  }
  .bg-gradient-secondary {
    background: linear-gradient(to right, rgb(109, 40, 217), rgb(79, 70, 229));
  }
  .glow-primary {
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.4), 0 0 40px rgba(139, 92, 246, 0.2);
  }
  .glow-blue {
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.4), 0 0 40px rgba(59, 130, 246, 0.2);
  }
  .glow-cyan {
    box-shadow: 0 0 20px rgba(34, 211, 238, 0.4), 0 0 40px rgba(34, 211, 238, 0.2);
  }
  .glassmorphism {
    backdrop-filter: blur(40px);
    background-color: rgb(255 255 255 / 0.05);
    border: 1px solid rgb(255 255 255 / 0.1);
  }
  .glassmorphism-dark {
    backdrop-filter: blur(40px);
    background-color: rgb(0 0 0 / 0.3);
    border: 1px solid rgb(255 255 255 / 0.1);
  }
  .card-gradient {
    position: relative;
    overflow: hidden;
    border-radius: 1rem;
    background: linear-gradient(135deg, rgba(139, 92, 246, 0.1) 0%, rgba(59, 130, 246, 0.05) 100%);
  }
  .animate-float {
    animation: float 3s ease-in-out infinite;
  }
  .animate-float-delayed {
    animation: float 4s ease-in-out infinite 0.5s;
  }
  .animate-glow-pulse {
    animation: glow-pulse 3s ease-in-out infinite;
  }
  .text-gradient {
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(135deg, #8b5cf6 0%, #3b82f6 50%, #22d3ee 100%);
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}
@keyframes glow-pulse {
  0%, 100% {
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.4), 0 0 40px rgba(139, 92, 246, 0.2);
  }
  50% {
    box-shadow: 0 0 30px rgba(139, 92, 246, 0.6), 0 0 60px rgba(139, 92, 246, 0.3);
  }
}
html {
  scroll-behavior: smooth;
}
